.App {
  text-align: center;
}




.App-header { display: inline-block; flex-direction: column; align-items: center; justify-content: center; line-height: 2em; margin: 15vh 5vh 5vh 5vh; width: 90%; color: #241F20;}
@media screen and (min-width: 993px) { body { font-size: calc(8vh + 2vmin); }}
@media screen and (max-width: 992px) { body { font-size: calc(6vh + 2vmin); }}
@media screen and (max-width: 600px) { body { font-size: calc(4vh + 3vmin); }}





img.hidden {
  display: none;
}

.text #desc.hidden {
  display: none;
}

p.hidden {
  display: none;
}
.text #desc {
  display: inline-block;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(1vh + 1vmin);
  margin: 0vh 0vh 5vh 5vh;
  width: 80%;
  line-height: 1.2em;
  font-weight: bold;
  color: #241F20;
}

.body {
  transition: all 1s ease-in-out;
}
.text {
  transition: all 1s ease-in-out;
  text-align:center;
}

